.card {
    width: 100px;
    height: 150px;
    margin: 1rem;
    position: relative;
    -webkit-box-shadow: 4px 4px 10px rgb(33, 33, 34);
            box-shadow: 4px 4px 10px rgb(33, 33, 34);
    -webkit-transition: all .2s ease-in-out;
    -o-transition: all .2s ease-in-out;
    transition: all .2s ease-in-out;
  }

  .img-container{
    border: 4px rgb(162, 173, 173);
    border-radius: 4px;
  }

  .card > .img-container {
    height: 100%;
    overflow: hidden;
    background: #8a8888;
  }
  
  .card > .img-container > img {
    height: 100%;
    width: 100%;
  }
  
  .card:hover {
    -webkit-box-shadow: 8px 8px 20px rgb(27, 27, 27);
            box-shadow: 8px 8px 20px rgb(27, 27, 27);
    -webkit-transform: scale(1.1);
        -ms-transform: scale(1.1);
            transform: scale(1.1);
    cursor: pointer;
  }
  
  .card:active {
    -webkit-transform: translate(1px, 1px);
        -ms-transform: translate(1px, 1px);
            transform: translate(1px, 1px);
  }


