.wrapper {
    background: #29339B;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-flow: row wrap;
        flex-flow: row wrap;
    -ms-flex-pack: distribute;
        justify-content: space-around;
    -ms-flex-line-pack: start;
        align-content: flex-start;
    overflow: auto;
    padding-bottom: 50px;

    width: 100vw;
    height: 100%;
    max-width: 100vw;
    max-height: 100%;
    /* overflow: hidden; */
    background: -webkit-linear-gradient(135deg, #353435, #c20d0d, rgb(35, 35, 36), rgb(63, 6, 68));
    background: -o-linear-gradient(135deg, #353435, #c20d0d, rgb(35, 35, 36), rgb(63, 6, 68));
    background: linear-gradient(-45deg, #353435, #c20d0d, rgb(35, 35, 36), rgb(63, 6, 68));
    background-size: 400% 400%;
    -webkit-animation: Gradient 15s ease infinite;
    animation: Gradient 15s ease infinite;
  }

  @-webkit-keyframes Gradient {
    0% {
      background-position: 0% 50%
    }
  
    50% {
      background-position: 100% 50%
    }
  
    100% {
      background-position: 0% 50%
    }
  }

  @keyframes Gradient {
    0% {
      background-position: 0% 50%
    }
  
    50% {
      background-position: 100% 50%
    }
  
    100% {
      background-position: 0% 50%
    }
  }


